.test-description {
  text-align: center;
  word-break: break-word;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  width: 80%;
  margin: auto;
  display: -webkit-box;
  overflow: hidden;
}

#wrapper {
  width: 100%;
  display: flex;
  position: relative;
  overflow: hidden;
}

.question-card {
  box-sizing: border-box;
  text-align: center;
  min-width: 100%;
  padding: 20px;
  transition: transform .5s ease-in-out;
  transform: translateX(0%);
}

.q-image {
  object-fit: contain;
  width: 100%;
  max-width: 16rem;
}

.answers {
  padding-top: .25rem;
}

.answers input[type="text"] {
  border-radius: unset;
  background: none;
  border: none;
  border-bottom: 1px solid #000;
  outline: none;
  width: 60%;
}

.answers label {
  text-align: start;
  margin-top: .5rem;
  background-color: #c29797;
  border-radius: .75rem;
  align-items: center;
  width: 60%;
  margin-inline: auto;
  padding: 1rem;
  transition: all .5s;
  display: flex;
}

.answers label input {
  width: auto;
  margin-right: .75rem;
  transition: all .5s;
}

#next, #prev {
  z-index: 10;
  cursor: pointer;
  pointer-events: all;
  color: #fff;
  background: #28a725;
  border: none;
  border-radius: 4px;
  outline: none;
  justify-content: space-between;
  padding: 1rem;
  font-size: 1.5rem;
  transition: all .2s;
  display: flex;
  position: absolute;
}

#next:hover, #prev:hover {
  background: #0a6c08;
}

#next:disabled, #prev:disabled {
  pointer-events: none;
  background: #60af5f;
}

#prev {
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
}

#next {
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
}

#finish {
  cursor: pointer;
  pointer-events: all;
  opacity: 1;
  background: none;
  border: none;
  border-left: 1px solid #28a725;
  outline: none;
  font-size: 1rem;
  font-weight: bold;
  transition: all .2s;
}

#finish:disabled {
  opacity: .5;
  pointer-events: none;
  border-left: 1px solid #28a72550;
}
